.profileInfo {
  position: relative;
  display: flex;
  align-items: center;
  min-height: 500px;
  background-color: yellow;
  overflow: hidden;
}

.wallpaper {
  position: absolute;
  inset: 0;
  z-index: 1;
}

.wallpaper img {
  width: 100%;
}

.descriptionBox {
  padding: 10px;
  width: 100%;
  min-height: 300px;
  height: max-content;
  z-index: 10;
}

.description {
  display: flex;
  min-height: 100%;
  width: 100%;
}

.avatarBox {
  height: 300px;
  min-width: 300px;
  margin-right: 20px;
  border-radius: 30px;
  overflow: hidden;
  background: rgba(206, 177, 15, 0.541);
}

.avatarBox img {
  width: 100%;
  height: auto;
}

.userInfo {
  width: 100%;
  min-height: 100%;
  padding: 10px;
  border-radius: 30px;
  background: rgba(206, 177, 15, 0.541);
}

.userInfo * {
  margin: 0;
}

.fullName {
  text-align: center;
}

.addInfo {
  display: flex;
  justify-content: space-between;
}

.addInfo div {
  width: 50%;
}
